import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

const GenderAgeHistogram = () => {
    const chartRef = useRef();

    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // ❗ null 성별 데이터 필터링 및 null 나이값을 '나이 미상'으로 변환
            const filteredData = data
                .filter(d => d.성별 !== null)
                .map(d => ({
                    ...d,
                    나이: d.나이 === null ? '나이 미상' : d.나이
                }));

            // SVG 설정
            const svgWidth = 800;
            const svgHeight = 500;
            const margin = { top: 50, right: 50, bottom: 100, left: 70 };

            const svg = d3.select(chartRef.current)
                .attr('width', svgWidth)
                .attr('height', svgHeight)
                .style('display', 'block')
                .style('margin-left', '0');

            // 나이대 데이터 그룹화
            const ageGroups = d3.group(filteredData, d => d.나이);

            // X축과 Y축 Scale 설정
            const xScale = d3.scaleBand()
                .domain([...ageGroups.keys()])
                .range([margin.left, svgWidth - margin.right])
                .padding(0.2);

            const yScale = d3.scaleLinear()
                .domain([0, d3.max(filteredData, d => d['투어 방문 횟수'])])
                .range([svgHeight - margin.bottom, margin.top]);

            // 색상 설정
            const colorScale = d3.scaleOrdinal()
                .domain(['남', '여'])
                .range(['#4A90E2', '#F78DA7']);

            // X축 추가
            svg.append('g')
                .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
                .call(d3.axisBottom(xScale).tickSize(0))
                .selectAll('text')
                .style('font-size', '12px')
                .style('text-anchor', 'middle');

            // Y축 추가
            svg.append('g')
                .attr('transform', `translate(${margin.left},0)`)
                .call(d3.axisLeft(yScale).ticks(10))
                .selectAll('text')
                .style('font-size', '12px');

            // Bar 너비 설정
            const barWidth = xScale.bandwidth() / 2;

            // Bar 추가 (성별에 따라 옆으로 배치)
            svg.selectAll('.bar')
                .data(filteredData)
                .enter()
                .append('rect')
                .attr('x', d => {
                    const baseX = xScale(d.나이);
                    return d.성별 === '남' ? baseX : baseX + barWidth;
                })
                .attr('y', d => yScale(d['투어 방문 횟수']))
                .attr('width', barWidth - 5) // 간격 추가
                .attr('height', d => svgHeight - margin.bottom - yScale(d['투어 방문 횟수']))
                .attr('fill', d => colorScale(d.성별));

            // 범례 추가
            const legend = svg.append('g')
                .attr('transform', `translate(${svgWidth - 150}, 30)`);

            legend.append('rect')
                .attr('x', 0)
                .attr('y', 0)
                .attr('width', 20)
                .attr('height', 20)
                .attr('fill', '#4A90E2');
            legend.append('text')
                .attr('x', 30)
                .attr('y', 15)
                .text('남성')
                .style('font-size', '14px');

            legend.append('rect')
                .attr('x', 0)
                .attr('y', 30)
                .attr('width', 20)
                .attr('height', 20)
                .attr('fill', '#F78DA7');
            legend.append('text')
                .attr('x', 30)
                .attr('y', 45)
                .text('여성')
                .style('font-size', '14px');
        });
    }, []);

    return <svg ref={chartRef}></svg>;
};

export default GenderAgeHistogram;
