import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';

// 페이지들 임포트
import HomePage from './pages/HomePage';
import BasicAnalyticsPage from './pages/BasicAnalyticsPage';
import DFMonthVisualizationPage from './pages/DFMonthVisualizationPage';
import TourPreferencePage from './pages/TourPreferencePage';
import SatisfactionAnalysisPage from './pages/SatisfactionAnalysisPage';

// 공통 링크 스타일
const navLinkStyle = {
    textDecoration: 'none',
    color: '#333',
    fontWeight: 'bold'
};

// 푸터 링크 스타일
const footerLinkStyle = {
    textDecoration: 'none',
    color: '#777'
};

function App() {
    const [showNav, setShowNav] = useState(true);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 100) {
                setShowNav(false);
            } else {
                setShowNav(true);
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    return (
        <Router>
            <div style={{ fontFamily: 'sans-serif', backgroundColor: '#f9f9f9', minHeight: '100vh' }}>

                {/* 상단 네비게이션 바 */}
                <header
                    style={{
                        ...headerStyle,
                        transform: showNav ? 'translateY(0)' : 'translateY(-100%)',
                        transition: 'transform 0.3s ease-in-out'
                    }}
                >
                    <nav style={navStyle}>
                        {/* 로고 + 웹사이트명 */}
                        <div style={logoStyle}>
                            <img
                                src="https://static.wixstatic.com/media/6b77ed_0f6de4b0aa2d45e188f700fb3cc94187~mv2.jpg"
                                alt="SeoulMate Logo"
                                style={{ height: 40, marginRight: 10 }}
                            />
                            <h2 style={{ margin: 0, color: '#333' }}>
                                SeoulMate Data Analytics
                            </h2>
                        </div>

                        {/* 네비게이션 링크 */}
                        <ul style={navLinksStyle}>
                            <li><Link to="/" style={navLinkStyle}>Home</Link></li>
                            <li><Link to="/basic-analytics" style={navLinkStyle}>Basic Analytics</Link></li>
                            <li><Link to="/df-month" style={navLinkStyle}>DF Month</Link></li>
                            <li><Link to="/tour-preference" style={navLinkStyle}>Tour Preference</Link></li>
                            <li><Link to="/satisfaction" style={navLinkStyle}>Satisfaction</Link></li>
                        </ul>

                    </nav>
                </header>

                {/* 메인 콘텐츠 */}
                <main style={mainContentStyle}>
                    <Routes>
                        <Route path="/" element={<HomePage />} />
                        <Route path="/basic-analytics" element={<BasicAnalyticsPage />} />
                        <Route path="/df-month" element={<DFMonthVisualizationPage />} />
                        <Route path="/tour-preference" element={<TourPreferencePage />} />
                        <Route path="/satisfaction" element={<SatisfactionAnalysisPage />} />
                    </Routes>
                </main>

                {/* 하단 Footer */}
                <footer style={footerStyle}>
                    <p style={{ margin: 0, fontSize: '0.9rem', color: '#777' }}>
                        &copy; {new Date().getFullYear()} SeoulMate Korea.
                        {' '}| <a href="https://www.seoulmatekorea.com/" target="_blank" rel="noreferrer" style={footerLinkStyle}>Official Website</a>
                        {' '}| <a href="https://www.instagram.com/seoulmatekorea/" target="_blank" rel="noreferrer" style={footerLinkStyle}>Instagram</a>
                    </p>
                </footer>
            </div>
        </Router>
    );
}

// 스타일 정의
const headerStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    backgroundColor: '#fff',
    borderBottom: '1px solid #eee',
    padding: '1em 2em',
    zIndex: 1000
};



const logoStyle = {
    display: 'flex',
    alignItems: 'center'
};

const navStyle = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    flexDirection: 'row',
    paddingRight: '2em', // 오른쪽 패딩 추가
};

const navLinksStyle = {
    listStyle: 'none',
    display: 'flex',
    gap: '1rem', // gap을 줄여서 화면에 맞게 조정
    margin: 0,
    marginRight: '2%', // marginRight로 수정
    padding: 0,
    flexWrap: 'wrap',
    justifyContent: 'flex-end',
};

const responsiveNavStyle = {
    '@media (max-width: 768px)': {
        flexDirection: 'column',
        alignItems: 'flex-start',
        gap: '0.5rem', // 작은 화면에서 gap 줄이기
    },
};



const mainContentStyle = {
    padding: '6em 2em 2em',  // 헤더 높이만큼 상단 패딩 추가
};

const footerStyle = {
    backgroundColor: '#fff',
    borderTop: '1px solid #eee',
    padding: '1em 2em',
    textAlign: 'center',
};

export default App;
