import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

const TourVisitFrequencyHistogram = () => {
    const chartRef = useRef();

    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // null 성별 데이터를 필터링
            const filteredData = data.filter(d => d.성별 !== null);

            // SVG 설정
            const svgWidth = 800;
            const svgHeight = 500;
            const margin = { top: 50, right: 50, bottom: 100, left: 70 };

            const svg = d3.select(chartRef.current)
                .attr('width', svgWidth)
                .attr('height', svgHeight)
                .style('display', 'block')
                .style('margin-left', '0'); // 좌측 정렬

            // 성별로 데이터 그룹화
            const genderGroups = d3.group(filteredData, d => d.성별);

            // 투어 방문 횟수를 기준으로 히스토그램을 그릴 구간 설정
            const histogram = d3.histogram()
                .value(d => d['투어 방문 횟수'])
                .domain([0, d3.max(filteredData, d => d['투어 방문 횟수'])])
                .thresholds(10); // 구간의 개수

            const bins = histogram(filteredData);

            // Y축과 X축 Scale 설정
            const xScale = d3.scaleLinear()
                .domain([0, d3.max(filteredData, d => d['투어 방문 횟수'])])
                .range([margin.left, svgWidth - margin.right]);

            const yScale = d3.scaleLinear()
                .domain([0, d3.max(bins, d => d.length)])
                .range([svgHeight - margin.bottom, margin.top]);

            // X축 추가
            svg.append('g')
                .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
                .call(d3.axisBottom(xScale));

            // Y축 추가
            svg.append('g')
                .attr('transform', `translate(${margin.left},0)`)
                .call(d3.axisLeft(yScale));

            // 성별을 기준으로 색상 구분
            const colorScale = d3.scaleOrdinal()
                .domain(['남', '여'])
                .range(['#4A90E2', '#F78DA7']);

            // Bar 그리기
            genderGroups.forEach((values, gender) => {
                const genderBins = histogram(values);

                svg.selectAll(`.bar-${gender}`)
                    .data(genderBins)
                    .enter()
                    .append('rect')
                    .attr('x', d => xScale(d.x0) + (xScale(d.x1) - xScale(d.x0)) / 2 - (xScale(d.x1) - xScale(d.x0)) / 2) // 막대가 중앙에 위치하도록 수정
                    .attr('y', d => yScale(d.length))
                    .attr('width', d => xScale(d.x1) - xScale(d.x0) - 1) // 구간 너비
                    .attr('height', d => svgHeight - margin.bottom - yScale(d.length))
                    .attr('fill', colorScale(gender));
            });

            // 범례 추가 (남성, 여성)
            const legend = svg.append('g')
                .attr('transform', `translate(${svgWidth - 150}, 30)`);

            legend.append('rect')
                .attr('x', 0)
                .attr('y', 0)
                .attr('width', 20)
                .attr('height', 20)
                .attr('fill', '#4A90E2');
            legend.append('text')
                .attr('x', 30)
                .attr('y', 15)
                .text('남성')
                .style('font-size', '14px');

            legend.append('rect')
                .attr('x', 0)
                .attr('y', 30)
                .attr('width', 20)
                .attr('height', 20)
                .attr('fill', '#F78DA7');
            legend.append('text')
                .attr('x', 30)
                .attr('y', 45)
                .text('여성')
                .style('font-size', '14px');
        });
    }, []);

    return <svg ref={chartRef}></svg>;
};

export default TourVisitFrequencyHistogram;
