import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const TourGenderStackedChart = () => {
    const chartRef = useRef();
    const [stackedData, setStackedData] = useState(null);

    useEffect(() => {
        d3.json('/df_month.json').then(data => {
            // (방문투어, 성별) => count
            const grouped = d3.rollups(
                data,
                v => v.length,
                d => d['방문투어'],
                d => d['성별']
            );
            // 모든 성별 목록
            const genders = new Set();
            grouped.forEach(([tour, arr]) => {
                arr.forEach(([gender]) => {
                    genders.add(gender);
                });
            });
            const allGenders = Array.from(genders);

            // Stacked 바차트용 객체 [{ tour, 남: xx, 여: yy, ... }]
            const final = grouped.map(([tour, arr]) => {
                const obj = { tour };
                arr.forEach(([gender, count]) => {
                    obj[gender] = count;
                });
                // 없으면 0
                allGenders.forEach(g => {
                    if (!obj[g]) obj[g] = 0;
                });
                return obj;
            });

            setStackedData({ data: final, genders: allGenders });
        });
    }, []);

    useEffect(() => {
        if (!stackedData) return;

        const { data, genders } = stackedData;

        const svgWidth = 900;
        const svgHeight = 500;
        const margin = { top: 50, right: 150, bottom: 100, left: 70 };

        d3.select(chartRef.current).selectAll('*').remove();
        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X: 방문투어
        const xScale = d3.scaleBand()
            .domain(data.map(d => d.tour))
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        // Stacked
        const stackGenerator = d3.stack()
            .keys(genders)
            .order(d3.stackOrderNone)
            .offset(d3.stackOffsetNone);

        const series = stackGenerator(data);

        // Y 최댓값
        const maxY = d3.max(data, d => {
            let sum = 0;
            genders.forEach(g => sum += d[g]);
            return sum;
        });
        const yScale = d3.scaleLinear()
            .domain([0, maxY])
            .range([svgHeight - margin.bottom, margin.top]);

        // 색상: 남=blue, 여=pink, 그 외?
        const colorMap = {
            '남': 'blue',
            '여': 'pink'
        };
        const color = g => colorMap[g] || 'gray';

        // rect
        svg.selectAll('g.layer')
            .data(series)
            .enter()
            .append('g')
            .attr('class', 'layer')
            .attr('fill', d => color(d.key))
            .selectAll('rect')
            .data(d => d)
            .enter()
            .append('rect')
            .attr('x', d => xScale(d.data.tour))
            .attr('y', d => yScale(d[1]))
            .attr('height', d => yScale(d[0]) - yScale(d[1]))
            .attr('width', xScale.bandwidth());

        // X축
        svg.append('g')
            .attr('transform', `translate(0, ${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform', 'rotate(-45)')
            .attr('text-anchor', 'end')
            .style('font-size', '12px');

        // Y축
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale));

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text('투어별, 성별 방문 빈도 수');

        // 범례
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right + 30}, ${margin.top})`);

        genders.forEach((g, i) => {
            const row = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            row.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(g));

            row.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(g);
        });
    }, [stackedData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourGenderStackedChart;
