import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const AgeGroupByContinentChart = () => {
    const chartRef = useRef();
    const [groupedData, setGroupedData] = useState([]);

    // 데이터 로드 및 전처리
    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // 대륙 매핑
            const continentMapping = {
                "미국": "North America",
                "독일": "Europe",
                "인도": "Asia",
                "캐나다": "North America",
                "브루나이 다루살람": "Asia",
                "홍콩": "Asia",
                "우즈베키스탄": "Asia",
                "아일랜드": "Europe",
                "남아프리카 공화국": "Africa",
                "이스라엘": "Asia",
                "프랑스": "Europe",
                "필리핀": "Asia",
                "키르기스스탄": "Asia",
                "방글라데시": "Asia",
                "스페인": "Europe",
                "콜롬비아": "South America",
                "베네수엘라": "South America",
                "이탈리아": "Europe",
                "싱가포르": "Asia",
                "덴마크": "Europe",
                "네팔 & 호주": "Oceania",
                "인도네시아": "Asia",
                "한국": "Asia",
                "중국": "Asia",
                "우크라이나": "Europe",
                "리투아니아": "Europe",
                "대만": "Asia",
                "스웨덴": "Europe",
                "일본": "Asia",
                "베트남": "Asia",
                "호주": "Oceania",
                "벨기에": "Europe",
                "네덜란드": "Europe",
                "에스토니아": "Europe",
                "네델란드": "Europe",
                "뉴질랜드": "Oceania",
                "타이완": "Asia",
                "페루": "South America",
                "러시아": "Europe",
                "카자흐스탄": "Asia",
                "스위스": "Europe",
                "말레이시아": "Asia",
                "마카오": "Asia",
                "파키스탄": "Asia",
                "영국": "Europe",
                "헝가리": "Europe",
                "미얀마": "Asia",
                "튀르키예": "Asia",
                "슬로베니아": "Europe",
                "루마니아": "Europe",
                "몽골": "Asia",
                "브라질": "South America",
                "보스니아": "Europe",
                "칠레": "South America",
            };

            // 나이가 null이면 '나이 미상'으로 변환
            const cleanData = data.map(d => ({
                ...d,
                나이: d.나이 === null ? '나이 미상' : d.나이
            }));

            // d3.rollups(데이터, 그룹별 집계함수, 레벨1key, 레벨2key)
            const nested = d3.rollups(
                cleanData,
                v => v.length,
                d => d.나이,
                d => continentMapping[d.국적] || '기타'
            );
            // nested 형태 예시:
            // [
            //   [ '30s', [ ['North America', 3], ['Asia', 5], ... ] ],
            //   [ 'Minors', [ ['North America', 1], ... ] ],
            //   ...
            // ]

            // 모든 대륙 목록 추출 (범례/스케일용)
            const allContinentsSet = new Set();
            nested.forEach(([_, arr]) => {
                arr.forEach(([continent]) => {
                    allContinentsSet.add(continent);
                });
            });
            const allContinents = Array.from(allContinentsSet);

            // [{ age, continentData: [{continent, count}, ...] }, ...]
            const finalData = nested.map(([age, arr]) => {
                const continentData = arr.map(([continent, count]) => ({ continent, count }));
                return { age, continentData };
            });

            // **정렬**: Minors → 맨 앞으로, 나이 미상 → 맨 뒤로
            const sortedData = finalData.sort((a, b) => {
                // 1. Minors 우선
                if (a.age === 'Minors' && b.age !== 'Minors') return -1;
                if (b.age === 'Minors' && a.age !== 'Minors') return 1;
                // 2. 나이 미상 마지막
                if (a.age === '나이 미상') return 1;
                if (b.age === '나이 미상') return -1;
                // 3. 그 외는 문자열 비교 (ex: 'early 20s', 'late 20s')
                return a.age.localeCompare(b.age);
            });

            setGroupedData({
                data: sortedData,
                continents: allContinents
            });
        });
    }, []);

    // 차트 생성
    useEffect(() => {
        if (!groupedData.data || groupedData.data.length === 0) return;

        const data = groupedData.data;
        const continents = groupedData.continents;

        const svgWidth = 800;
        const svgHeight = 500;
        const margin = { top: 50, right: 50, bottom: 100, left: 70 };

        // 이전 내용 제거
        d3.select(chartRef.current).selectAll('*').remove();

        // SVG 설정
        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight)
            .style('display', 'block')
            .style('margin-left', '0'); // 좌측 정렬

        // X축: 나이
        const x0 = d3.scaleBand()
            .domain(data.map(d => d.age))
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        // X1: 대륙 (Grouped)
        const x1 = d3.scaleBand()
            .domain(continents)
            .range([0, x0.bandwidth()])
            .padding(0.1);

        // Y축: count
        const y = d3.scaleLinear()
            .domain([0, d3.max(data, d => d3.max(d.continentData, dd => dd.count))])
            .nice()
            .range([svgHeight - margin.bottom, margin.top]);

        // 색상 스케일
        const color = d3.scaleOrdinal(d3.schemeSet3)
            .domain(continents);

        // X축
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(x0))
            .selectAll('text')
            .style('font-size', '12px')
            .style('text-anchor', 'middle');

        // Y축
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(y))
            .selectAll('text')
            .style('font-size', '12px');

        // 그룹 막대
        svg.selectAll('.ageGroup')
            .data(data)
            .enter()
            .append('g')
            .attr('class', 'ageGroup')
            .attr('transform', d => `translate(${x0(d.age)},0)`)
            .selectAll('rect')
            .data(d => d.continentData)
            .enter()
            .append('rect')
            .attr('x', d => x1(d.continent))
            .attr('y', d => y(d.count))
            .attr('width', x1.bandwidth())
            .attr('height', d => svgHeight - margin.bottom - y(d.count))
            .attr('fill', d => color(d.continent));

        // 범례
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right - 100},${margin.top})`);

        continents.forEach((cont, i) => {
            const legendRow = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            legendRow.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(cont));

            legendRow.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(cont);
        });

    }, [groupedData]);

    return (
        <div>
            <h3>Age Group Distribution by Continent</h3>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default AgeGroupByContinentChart;
