import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const TourPopularityBarChart = () => {
    const chartRef = useRef();
    const [tourData, setTourData] = useState([]);

    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // '투어_1' 빈도 계산
            const grouped = d3.rollups(
                data,
                v => v.length,
                d => d['투어_1']
            );

            // null 제외 & 내림차순 정렬
            const filtered = grouped
                .filter(([tourName]) => tourName !== null)
                .sort((a, b) => b[1] - a[1]);

            const finalData = filtered.map(([tourName, count]) => ({
                tourName,
                count
            }));

            setTourData(finalData);
        });
    }, []);

    useEffect(() => {
        if (tourData.length === 0) return;

        const svgWidth = 900;
        const svgHeight = 500;
        const margin = { top: 50, right: 50, bottom: 100, left: 70 };

        // 기존 요소 제거
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X축, Y축 스케일
        const xScale = d3.scaleBand()
            .domain(tourData.map(d => d.tourName))
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        const yScale = d3.scaleLinear()
            .domain([0, d3.max(tourData, d => d.count)])
            .nice()
            .range([svgHeight - margin.bottom, margin.top]);

        // Viridis 컬러 스케일
        // 막대가 여러 개일 때, 데이터 개수 범위(0~N-1)에 맞춰 색을 분포시킵니다.
        const colorScale = d3.scaleSequential()
            .domain([0, tourData.length - 1])
            .interpolator(d3.interpolateViridis);

        // Bar
        svg.selectAll('.bar')
            .data(tourData)
            .enter()
            .append('rect')
            .attr('class', 'bar')
            .attr('x', d => xScale(d.tourName))
            .attr('y', d => yScale(d.count))
            .attr('width', xScale.bandwidth())
            .attr('height', d => (svgHeight - margin.bottom) - yScale(d.count))
            // index(i)를 사용해 colorScale(i)로 Viridis 컬러 매핑
            .attr('fill', (d, i) => colorScale(i));

        // X축
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .style('text-anchor', 'end')
            .style('font-size', '12px')
            .attr('dx', '-0.5em')
            .attr('dy', '0.5em')
            .attr('transform', 'rotate(-45)');

        // Y축
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .selectAll('text')
            .style('font-size', '12px');

        // Bar 위에 텍스트(방문 횟수) 표시
        svg.selectAll('.text')
            .data(tourData)
            .enter()
            .append('text')
            .attr('class', 'text')
            .attr('x', d => xScale(d.tourName) + xScale.bandwidth() / 2)
            .attr('y', d => yScale(d.count) - 5)
            .attr('text-anchor', 'middle')
            .style('font-size', '12px')
            .text(d => d.count);

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .attr('font-size', '16px')
            .text('Popularity of Tours (Viridis)');

        // X label
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', svgHeight - 5)
            .attr('text-anchor', 'middle')
            .attr('font-size', '14px')
            .text('Tour Name');

        // Y label
        svg.append('text')
            .attr('transform', 'rotate(-90)')
            .attr('x', -(svgHeight / 2))
            .attr('y', margin.left - 50)
            .attr('text-anchor', 'middle')
            .attr('font-size', '14px')
            .text('Visit Count');

    }, [tourData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourPopularityBarChart;
