/** 배경 히어로 이미지 */
const SEOULMATE_HERO_URL =
    "https://static.wixstatic.com/media/6b77ed_8b2841067d6b47148e1c387058fea4c3~mv2.jpg";

function HomePage() {
    // (1) 스크롤 로직: 300px 이상일 때 fixed → absolute 전환
    const [isFixed, setIsFixed] = useState(true);
    const [absoluteTop, setAbsoluteTop] = useState(-745); // 원하는 위치로 설정
    const worldMapRef = useRef(null);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 600 && isFixed) {
                setIsFixed(false);
            } else if (window.scrollY <= 600 && !isFixed) {
                setIsFixed(true);
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [isFixed]);

    return (
        <div style={pageContainerStyle}>

            {/* (A) 월드맵 백그라운드 */}
            <div
                ref={worldMapRef}
                style={{
                    ...worldMapBackgroundStyle,
                    position: isFixed ? 'fixed' : 'absolute',
                    top: isFixed ? '0' : `${absoluteTop}px`,
                }}
            >
                <WorldMap />
            </div>


            {/* (B) 히어로 섹션 */}
            <div style={heroSectionStyle}>
                <div style={heroOverlayStyle}>
                    <h2 style={heroOverlayTitle}>
                        Explore Seoul with <strong>SeoulMate</strong>
                    </h2>
                    <p style={heroOverlaySubtitle}>
                        Welcome to our Data Analytics platform!
                    </p>
                </div>
            </div>

            {/* (C) 간단 소개 */}
            <h1 style={mainTitleStyle}>Welcome to SeoulMate Data Analytics</h1>
            <p style={mainParagraphStyle}>
                이곳은 서울메이트(SeoulMate)의 행사 및 투어 관련 데이터를 분석해,
                보다 나은 서비스를 위해 시각화한 결과를 공유하는 사이트입니다.
                <br />
                상단 메뉴를 통해 다양한 분석 차트(연령대별, 국적별, 만족도, 재방문율 등)를 만나보세요.
            </p>

            {/* (E) 기존 Moment Gallery */}
            <section style={gallerySectionStyle}>
                <h2 style={galleryTitleStyle}>SeoulMate Moments</h2>
                <p style={gallerySubtitleStyle}>
                    아래 사진들은 기존 갤러리 사진입니다.
                </p>
                <div style={galleryGridStyle}>
                    {SEOULMATE_IMAGE_GALLERY.map((url, index) => (
                        <img
                            key={index}
                            src={url}
                            alt={`SeoulMate Gallery ${index + 1}`}
                            style={galleryImageStyle}
                        />
                    ))}
                </div>
            </section>

            {/* (F) 뉴스레터 이미지 섹션 */}
            <section style={newsletterSectionStyle}>
                <h2 style={newsletterTitleStyle}>SeoulMate NewsLetters</h2>
                <p style={newsletterSubtitleStyle}>
                    최근 발행된 뉴스레터를 사진으로 만나보세요.
                </p>
                <div style={newsletterGridStyle}>
                    {NEWSLETTER_IMAGES.map((item, idx) => (
                        <img
                            key={`newsletter-${idx}`}
                            src={item.url}
                            alt={`SeoulMate Newsletter ${idx + 1}`}
                            style={newsletterImageStyle}
                        />
                    ))}
                </div>
            </section>



            {/* 하단 여백 */}
            <div style={{ height: '2em' }} />
        </div>
    );
}

