import React, { useEffect, useRef } from "react";
import * as d3 from "d3";
import * as topojson from "topojson-client";

const WorldMap = () => {
    const canvasRef = useRef();

    useEffect(() => {
        const width = window.innerWidth * 1.5;  // 기존보다 더 큰 캔버스
        const height = window.innerHeight * 1.5;
        const dpr = window.devicePixelRatio || 1;

        const canvas = d3.select(canvasRef.current)
            .attr("width", dpr * width)
            .attr("height", dpr * height)
            .style("width", `${width}px`)
            .style("height", `${height}px`);

        const context = canvas.node().getContext("2d");
        context.scale(dpr, dpr);

        const projection = d3.geoOrthographic()
            .fitExtent([[10, 10], [width - 10, height - 10]], { type: "Sphere" });

        const path = d3.geoPath(projection, context);

        // 색상 스케일 설정
        const colorScale = d3.scaleOrdinal(d3.schemeSet3);

        Promise.all([
            d3.json("https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json"),
            d3.json("nationality_tour_visits_english.json")
        ]).then(([worldData, visitData]) => {
            const land = topojson.feature(worldData, worldData.objects.land);
            const countries = topojson.feature(worldData, worldData.objects.countries).features;
            const borders = topojson.mesh(worldData, worldData.objects.countries, (a, b) => a !== b);

            // 나라별 고유 색상 맵
            const countryColors = new Map();
            countries.forEach((country, index) => {
                countryColors.set(country, colorScale(index));
            });

            // 방문 순서대로 국가 정렬
            const sortedVisits = visitData.sort((a, b) => b.visitCount - a.visitCount);

            // 국가 이름으로 매핑
            const countryByName = new Map();
            countries.forEach(country => {
                const name = country.properties.name;
                if (name) countryByName.set(name, country);
            });

            // 방문할 국가 목록 생성
            const visitCountries = sortedVisits
                .map(visit => countryByName.get(visit.country))
                .filter(Boolean); // 유효한 국가만 포함

            let p1, p2 = [0, 0], r1, r2 = [0, 0, 0];
            const tilt = 20;

            function animateCountries() {
                let index = 0;

                function loopAnimation() {
                    animateCountry(visitCountries[index])
                        .then(() => {
                            index = (index + 1) % visitCountries.length; // 마지막 나라 이후 첫 번째 나라로 돌아감
                            loopAnimation();
                        });
                }

                loopAnimation();
            }

            function render(country, arc, highlight = false, visitInfo = null) {
                context.clearRect(0, 0, width, height);

                // 물 배경
                context.beginPath();
                path({ type: "Sphere" });
                context.fillStyle = "#ADD8E6"; // 진한 파란색 물 배경
                context.fill();

                // 각 나라 색상
                countries.forEach(c => {
                    context.beginPath();
                    path(c);
                    context.fillStyle = countryColors.get(c); // 고유 색상
                    context.fill();
                });

                // 기본 국경선
                context.beginPath();
                path(borders);
                context.strokeStyle = "#fff"; // 기본 얇은 검정색 국경선
                context.lineWidth = 0.5;
                context.stroke();

                // 하이라이트된 나라의 외곽선 (국경선 + 바다와 맞닿은 부분)
                if (country) {
                    context.beginPath();
                    path(country);
                    context.strokeStyle = highlight ? "#000" : "#fff"; // 하이라이트 시 하얀색, 아니면 기본 검정색
                    context.lineWidth = highlight ? 2 : 0.5;
                    context.stroke();
                }

                // 이동 경로 그리기
                if (arc) {
                    context.beginPath();
                    path(arc);
                    context.strokeStyle = "#000"; // 경로 색상
                    context.lineWidth = 2;
                    context.stroke();
                }

                // 하이라이트된 국가 이름과 방문자 수 표시
                if (visitInfo) {
                    const boxWidth = 240;
                    const boxHeight = 60;
                    const boxX = (width - boxWidth) / 2;
                    const boxY = (height / 2) - 50; // 더 아래로 이동

                    // 둥근 모서리 박스 그리기
                    context.beginPath();
                    context.moveTo(boxX + 10, boxY);
                    context.lineTo(boxX + boxWidth - 10, boxY);
                    context.quadraticCurveTo(boxX + boxWidth, boxY, boxX + boxWidth, boxY + 10);
                    context.lineTo(boxX + boxWidth, boxY + boxHeight - 10);
                    context.quadraticCurveTo(boxX + boxWidth, boxY + boxHeight, boxX + boxWidth - 10, boxY + boxHeight);
                    context.lineTo(boxX + 10, boxY + boxHeight);
                    context.quadraticCurveTo(boxX, boxY + boxHeight, boxX, boxY + boxHeight - 10);
                    context.lineTo(boxX, boxY + 10);
                    context.quadraticCurveTo(boxX, boxY, boxX + 10, boxY);
                    context.closePath();

                    // 박스 스타일 적용
                    context.fillStyle = "#fff";
                    context.shadowColor = "rgba(0, 0, 0, 0.2)";
                    context.shadowBlur = 10;
                    context.fill();

                    // 박스 안 텍스트 스타일
                    context.shadowColor = "transparent"; // 텍스트에 그림자 제거
                    context.font = "16px Arial";
                    context.fillStyle = "#000";
                    context.textAlign = "center";
                    context.fillText(`${visitInfo.country}: ${visitInfo.visitCount} visits`, width / 2, boxY + 30);
                }
            }

            function animateCountry(country) {
                return new Promise(resolve => {
                    const visitInfo = sortedVisits.find(v => v.country === country.properties.name);
                    // 이전 위치와 새로운 위치 설정
                    p1 = p2;
                    p2 = d3.geoCentroid(country);
                    r1 = r2;
                    r2 = [-p2[0], tilt - p2[1], 0];

                    const ip = d3.geoInterpolate(p1, p2);
                    const iv = Versor.interpolateAngles(r1, r2);


                    // 회전 애니메이션
                    d3.transition()
                        .duration(3000)
                        .tween("render", () => t => {
                            projection.rotate(iv(t)); // 회전 각도 갱신
                            render(country, { type: "LineString", coordinates: [p1, ip(t)] }, true);
                        })
                        .on("end", () => {
                            // 도착 시 선이 지워지는 애니메이션
                            d3.transition()
                                .duration(2000)
                                .tween("fadeLine", () => t => {
                                    const fadeArc = {
                                        type: "LineString",
                                        coordinates: [ip(t), p2]
                                    };
                                    render(country, fadeArc, true, visitInfo); // 하이라이트 유지
                                })
                                .on("end", () => {
                                    // 최종 회전 상태를 유지하고 다음 국가로 이동 준비
                                    projection.rotate(iv(1));
                                    resolve();
                                });
                        });

                });
            }

            animateCountries();
        });

        // Versor 클래스 정의
        class Versor {
            static fromAngles([l, p, g]) {
                l *= Math.PI / 360;
                p *= Math.PI / 360;
                g *= Math.PI / 360;
                const sl = Math.sin(l), cl = Math.cos(l);
                const sp = Math.sin(p), cp = Math.cos(p);
                const sg = Math.sin(g), cg = Math.cos(g);
                return [
                    cl * cp * cg + sl * sp * sg,
                    sl * cp * cg - cl * sp * sg,
                    cl * sp * cg + sl * cp * sg,
                    cl * cp * sg - sl * sp * cg
                ];
            }

            static toAngles([a, b, c, d]) {
                return [
                    Math.atan2(2 * (a * b + c * d), 1 - 2 * (b * b + c * c)) * 180 / Math.PI,
                    Math.asin(Math.max(-1, Math.min(1, 2 * (a * c - d * b)))) * 180 / Math.PI,
                    Math.atan2(2 * (a * d + b * c), 1 - 2 * (c * c + d * d)) * 180 / Math.PI
                ];
            }

            static interpolateAngles(a, b) {
                const i = Versor.interpolate(Versor.fromAngles(a), Versor.fromAngles(b));
                return t => Versor.toAngles(i(t));
            }

            static interpolate([a1, b1, c1, d1], [a2, b2, c2, d2]) {
                let dot = a1 * a2 + b1 * b2 + c1 * c2 + d1 * d2;
                if (dot < 0) {
                    a2 = -a2;
                    b2 = -b2;
                    c2 = -c2;
                    d2 = -d2;
                    dot = -dot;
                }

                if (dot > 0.9995) return Versor.interpolateLinear([a1, b1, c1, d1], [a2, b2, c2, d2]);

                const theta0 = Math.acos(Math.max(-1, Math.min(1, dot)));
                const x = new Array(4);
                const l = Math.hypot(a2 -= a1 * dot, b2 -= b1 * dot, c2 -= c1 * dot, d2 -= d1 * dot);
                a2 /= l;
                b2 /= l;
                c2 /= l;
                d2 /= l;

                return t => {
                    const theta = theta0 * t;
                    const s = Math.sin(theta);
                    const c = Math.cos(theta);
                    x[0] = a1 * c + a2 * s;
                    x[1] = b1 * c + b2 * s;
                    x[2] = c1 + c2 * s;
                    x[3] = d1 + d2 * s;
                    return x;
                };
            }

            static interpolateLinear([a1, b1, c1, d1], [a2, b2, c2, d2]) {
                a2 -= a1;
                b2 -= b1;
                c2 -= c1;
                d2 -= d1;
                const x = new Array(4);
                return t => {
                    const l = Math.hypot(
                        (x[0] = a1 + a2 * t),
                        (x[1] = b1 + b2 * t),
                        (x[2] = c1 + c2 * t),
                        (x[3] = d1 + d2 * t)
                    );
                    x[0] /= l;
                    x[1] /= l;
                    x[2] /= l;
                    x[3] /= l;
                    return x;
                };
            }
        }
    }, []);

    return <canvas ref={canvasRef}></canvas>;
};

export default WorldMap;
