import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

const GenderMap = () => {
    const chartRef = useRef();

    // 국가별 대륙 매핑
    const continentMapping = {
        "미국": "North America",
        "독일": "Europe",
        "인도": "Asia",
        "캐나다": "North America",
        "브루나이 다루살람": "Asia",
        "홍콩": "Asia",
        "우즈베키스탄": "Asia",
        "아일랜드": "Europe",
        "남아프리카 공화국": "Africa",
        "이스라엘": "Asia",
        "프랑스": "Europe",
        "필리핀": "Asia",
        "키르기스스탄": "Asia",
        "방글라데시": "Asia",
        "스페인": "Europe",
        "콜롬비아": "South America",
        "베네수엘라": "South America",
        "이탈리아": "Europe",
        "싱가포르": "Asia",
        "덴마크": "Europe",
        "네팔 & 호주": "Oceania",
        "인도네시아": "Asia",
        "한국": "Asia",
        "중국": "Asia",
        "우크라이나": "Europe",
        "리투아니아": "Europe",
        "대만": "Asia",
        "스웨덴": "Europe",
        "일본": "Asia",
        "베트남": "Asia",
        "호주": "Oceania",
        "벨기에": "Europe",
        "네덜란드": "Europe",
        "에스토니아": "Europe",
        "네델란드": "Europe",
        "뉴질랜드": "Oceania",
        "타이완": "Asia",
        "페루": "South America",
        "러시아": "Europe",
        "카자흐스탄": "Asia",
        "스위스": "Europe",
        "말레이시아": "Asia",
        "마카오": "Asia",
        "파키스탄": "Asia",
        "영국": "Europe",
        "헝가리": "Europe",
        "미얀마": "Asia",
        "튀르키예": "Asia",
        "슬로베니아": "Europe",
        "루마니아": "Europe",
        "몽골": "Asia",
        "브라질": "South America",
        "보스니아": "Europe",
        "칠레": "South America",
    };

    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // ❗ null 성별 및 continentMapping에 없는 국가 필터링
            const filteredData = data.filter(d => d.성별 !== null && continentMapping[d.국적]);

            // SVG 설정
            const svgWidth = 1000;
            const svgHeight = 600;
            const margin = { top: 50, right: 50, bottom: 150, left: 50 };

            const svg = d3.select(chartRef.current)
                .attr('width', svgWidth)
                .attr('height', svgHeight)
                .style('display', 'block')
                .style('margin-left', '0'); // 좌측 정렬

            // X축과 Y축 설정
            const xScale = d3.scaleBand()
                .domain([...new Set(Object.values(continentMapping))])
                .range([margin.left, svgWidth - margin.right- 100])
                .padding(0.5);

            const yScale = d3.scaleLinear()
                .domain([0, d3.max(filteredData, d => d['투어 방문 횟수']) + 1])
                .range([svgHeight - margin.bottom, margin.top]);

            // 색상 설정 (남/여)
            const colorScale = d3.scaleOrdinal()
                .domain(['남', '여'])
                .range(['#4A90E2', '#F78DA7']);

            // X축 추가
            svg.append('g')
                .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
                .call(d3.axisBottom(xScale).tickSize(0))
                .selectAll('text')
                .style('text-anchor', 'middle')
                .style('font-size', '14px')
                .attr('transform', 'translate(0,10)'); // 여백 추가

            // Y축 추가
            svg.append('g')
                .attr('transform', `translate(${margin.left},0)`)
                .call(d3.axisLeft(yScale).ticks(10))
                .selectAll('text')
                .style('font-size', '14px');

            // 성별 그룹 설정
            const genderOffset = xScale.bandwidth() / 4;

            // 툴팁 생성
            const tooltip = d3.select('body')
                .append('div')
                .style('position', 'absolute')
                .style('background', '#fff')
                .style('border', '1px solid #ccc')
                .style('padding', '8px')
                .style('border-radius', '4px')
                .style('pointer-events', 'none')
                .style('opacity', 0);

            // 점(Scatter Plot) 추가
            svg.selectAll('circle')
                .data(filteredData)
                .enter()
                .append('circle')
                .attr('cx', d => {
                    const baseX = xScale(continentMapping[d.국적]);
                    const additionalOffset = 30;
                    return d.성별 === '남' ? baseX - genderOffset + additionalOffset: baseX + genderOffset + additionalOffset;
                })
                .attr('cy', d => yScale(d['투어 방문 횟수']))
                .attr('r', 10)
                .attr('fill', d => colorScale(d.성별))
                .attr('opacity', 0.8)
                .on('mouseover', (event, d) => {
                    tooltip.transition().duration(200).style('opacity', 1);
                    tooltip.html(`국가: ${d.국적}<br>방문 횟수: ${d['투어 방문 횟수']}`)
                        .style('left', (event.pageX + 15) + 'px')
                        .style('top', (event.pageY - 28) + 'px');
                })
                .on('mouseout', () => {
                    tooltip.transition().duration(200).style('opacity', 0);
                });
        });
    }, []);

    return <svg ref={chartRef}></svg>;
};

export default GenderMap;
