import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const TourCourseNationalityBar = () => {
    const chartRef = useRef();
    const [finalData, setFinalData] = useState([]);
    const [allTours, setAllTours] = useState([]);
    const [allNations, setAllNations] = useState([]);

    useEffect(() => {
        d3.json('/merged_df.json').then(rawData => {
            if (!rawData || !Array.isArray(rawData)) {
                console.error('merged_df.json is invalid or not an array');
                return;
            }
            // STEP 1) 데이터 펼치기 => (투어코스, 국적, 선호횟수) 배열
            // merged_df.json 구조:
            // [
            //   {
            //     "방문투어": "강남",
            //     "국적": {"대만":4, "스페인":4, ...},
            //     ...
            //   },
            //   ...
            // ]

            // 1. "방문투어" = "투어코스" 명
            // 2. "국적" key-value => 국적: 횟수
            // => { 투어코스: "강남", 국적: "대만", 선호횟수:4 }, ...
            let meltedData = [];
            rawData.forEach(item => {
                const tourName = item["방문투어"];
                if (!tourName || !item["국적"]) return;
                const nationObj = item["국적"];
                // 국적: { "대만":4, "스페인":4, ... }
                Object.entries(nationObj).forEach(([nation, count]) => {
                    meltedData.push({
                        투어코스: tourName,
                        국적: nation,
                        선호횟수: +count
                    });
                });
            });

            // STEP 2) (투어코스, 국적)별 집계 (이미 합산된 데이터지만 혹시 중복 항목이 있으면 추가)
            // 여기서는 meltedData가 이미 (투어코스, 국적) = 선호횟수 형태라면
            // 추가 집계 없이 그대로 사용 가능.
            // 만약 중복이 있다면 rollup으로 sum해도 됨:
            // const grouped = d3.rollups(
            //   meltedData,
            //   v => d3.sum(v, d => d.선호횟수),
            //   d => d.투어코스,
            //   d => d.국적
            // );
            // ...
            // 그러나 여기서는 meltedData로 충분하다고 가정.

            // STEP 3) 각 투어코스별 총합 >= 5 인지 검사
            // => 투어코스별 선호횟수 총합
            const totalByTour = d3.rollups(
                meltedData,
                v => d3.sum(v, d => d.선호횟수),
                d => d.투어코스
            );
            // => [ [ "강남", 13 ], ["경복궁", 36 ], ... ]
            const validTourSet = new Set(
                totalByTour
                    .filter(([_, sum]) => sum >= 5) // 총합 5 이상
                    .map(([tour]) => tour)
            );

            // filter: 유효한 투어코스만
            let filteredData = meltedData.filter(d => validTourSet.has(d.투어코스));

            // STEP 4) 각 투어코스별 상위 5개 국적
            // => groupBy(투어코스) => sort => slice(0,5)
            // grouped => Map(투어코스 => Array( {투어코스, 국적, 선호횟수}... ))
            const groupedByTour = d3.group(filteredData, d => d.투어코스);
            let final = [];
            groupedByTour.forEach((arr, tour) => {
                // sort desc by 선호횟수
                arr.sort((a,b) => b.선호횟수 - a.선호횟수);
                // 상위 5개만
                const top5 = arr.slice(0,5);
                final.push(...top5);
            });
            // => final = [ {투어코스, 국적, 선호횟수}, ... ]

            setFinalData(final);
        });
    }, []);

    useEffect(() => {
        if (!finalData.length) return;

        // x축: 투어코스
        // hue: 국적 => Grouped Bar
        // y: 선호횟수

        // 1) 유니크 투어코스, 국적 추출
        const uniqueTours = [...new Set(finalData.map(d => d.투어코스))];
        const uniqueNations = [...new Set(finalData.map(d => d.국적))];

        setAllTours(uniqueTours);
        setAllNations(uniqueNations);

        // 차트 크기
        const svgWidth = 1200;
        const svgHeight = 500;
        const margin = { top: 50, right: 100, bottom: 100, left: 70 };

        // clear
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X0: 투어코스
        const x0 = d3.scaleBand()
            .domain(uniqueTours)
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        // X1: 국적
        const x1 = d3.scaleBand()
            .domain(uniqueNations)
            .range([0, x0.bandwidth()])
            .padding(0.05);

        // Y: 선호횟수
        const maxVal = d3.max(finalData, d => d.선호횟수);
        const yScale = d3.scaleLinear()
            .domain([0, maxVal || 0])
            .nice()
            .range([svgHeight - margin.bottom, margin.top]);

        // Color: 국적
        // viridis, 국적 개수가 많다면 구분이 어려울 수 있음
        const color = d3.scaleOrdinal()
            .domain(uniqueNations)
            .range(d3.quantize(d3.interpolateViridis, uniqueNations.length));

        // 그룹 생성
        // => grouped bar: (tour) => g => (nation) => rect
        const group = svg.selectAll('.gTour')
            .data(uniqueTours)
            .enter()
            .append('g')
            .attr('class', 'gTour')
            .attr('transform', d => `translate(${x0(d)},0)`);

        group.selectAll('rect')
            .data(tour => {
                // tour에 해당하는 data만
                return finalData.filter(d => d.투어코스 === tour);
            })
            .enter()
            .append('rect')
            .attr('x', d => x1(d.국적))
            .attr('y', d => yScale(d.선호횟수))
            .attr('width', x1.bandwidth())
            .attr('height', d => (svgHeight - margin.bottom) - yScale(d.선호횟수))
            .attr('fill', d => color(d.국적));

        // X축
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(x0))
            .selectAll('text')
            .attr('transform', 'rotate(-45)')
            .style('text-anchor', 'end')
            .style('font-size', '12px');

        // Y축
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .selectAll('text')
            .style('font-size', '12px');

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text("투어코스별 국적 선호 횟수 (상위 5개 국적, 총합 5 이상 투어코스)");

        // X label
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', svgHeight - 10)
            .attr('text-anchor', 'middle')
            .style('font-size', '14px')
            .text("투어 코스");

        // Y label
        svg.append('text')
            .attr('transform', 'rotate(-90)')
            .attr('x', -(svgHeight / 2))
            .attr('y', margin.left - 50)
            .attr('text-anchor', 'middle')
            .style('font-size', '14px')
            .text("선호 횟수");

        // 범례
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right + 10}, ${margin.top})`);

        uniqueNations.forEach((nat, i) => {
            const row = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            row.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(nat));

            row.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(nat);
        });

    }, [finalData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourCourseNationalityBar;
