import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const TourMonthStackedChart = () => {
    const chartRef = useRef();
    const [stackedData, setStackedData] = useState(null);

    useEffect(() => {
        d3.json('/df_month.json').then(data => {
            // 1) (월, 방문투어)별 카운트
            // grouped => Map(월 => Map(방문투어 => count))
            const grouped = d3.rollups(
                data,
                v => v.length,
                d => d['월'],
                d => d['방문투어']
            );
            // 2) 모든 방문투어 목록
            const tourSet = new Set();
            grouped.forEach(([month, arr]) => {
                arr.forEach(([tour]) => {
                    tourSet.add(tour);
                });
            });
            const allTours = Array.from(tourSet);

            // 3) stacked bar용 데이터 { month, ...투어명들 }
            //    예: { month: 1, "경복궁": 10, "하늘공원": 5, ... }
            const final = grouped.map(([month, arr]) => {
                const obj = { month: +month }; // 숫자로 변환
                arr.forEach(([tour, count]) => {
                    obj[tour] = count;
                });
                // 없는 투어는 undefined => 0 처리
                allTours.forEach(t => {
                    if (!obj[t]) obj[t] = 0;
                });
                return obj;
            }).sort((a,b) => a.month - b.month); // 월 오름차순 정렬

            setStackedData({ data: final, tours: allTours });
        });
    }, []);

    useEffect(() => {
        if (!stackedData) return;

        const { data, tours } = stackedData;

        const svgWidth = 800;
        const svgHeight = 500;
        const margin = { top: 50, right: 150, bottom: 100, left: 70 };

        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // XScale: month
        const xScale = d3.scaleBand()
            .domain(data.map(d => d.month))
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        // Stack 생성
        const stackGenerator = d3.stack()
            .keys(tours)    // 모든 투어
            .order(d3.stackOrderNone)
            .offset(d3.stackOffsetNone);

        const series = stackGenerator(data);
        // series: [ Array(스택의 좌표), Array(스택의 좌표), ... ], 키별

        // Y 최댓값
        const maxY = d3.max(data, d => {
            let sum = 0;
            tours.forEach(t => sum += d[t]);
            return sum;
        });

        const yScale = d3.scaleLinear()
            .domain([0, maxY])
            .range([svgHeight - margin.bottom, margin.top]);

        const tab20Colors = [
            "#393b79","#5254a3","#6b6ecf","#9c9ede","#637939","#8ca252","#b5cf6b","#cedb9c",
            "#8c6d31","#bd9e39","#e7ba52","#e7cb94","#843c39","#ad494a","#d6616b","#e7969c",
            "#7b4173","#a55194","#ce6dbd","#de9ed6"
        ];

        // 컬러
        const color = d3.scaleOrdinal(tab20Colors)
            .domain(tours);

        // Stacked Bar
        svg.selectAll('g.layer')
            .data(series)
            .enter()
            .append('g')
            .attr('class', 'layer')
            .attr('fill', d => color(d.key))
            .selectAll('rect')
            .data(d => d)
            .enter()
            .append('rect')
            .attr('x', d => xScale(d.data.month))
            .attr('y', d => yScale(d[1]))
            .attr('height', d => yScale(d[0]) - yScale(d[1]))
            .attr('width', xScale.bandwidth());

        // X축
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale).tickFormat(d => `${d}월`))
            .selectAll('text')
            .style('font-size', '12px')
            .attr('transform', 'rotate(-45)')
            .attr('text-anchor', 'end');

        // Y축
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale));

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text('투어별, 월별 방문 빈도 수');

        // 범례
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right + 20}, ${margin.top})`);

        tours.forEach((t, i) => {
            const row = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            row.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(t));

            row.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(t);
        });

    }, [stackedData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourMonthStackedChart;
