import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const GenderPieChart = () => {
    const chartRef = useRef();
    const [genderData, setGenderData] = useState([]);

    // 성별별 방문 횟수 데이터 계산
    useEffect(() => {
        d3.json('/tour_gender_data.json').then(data => {
            // 성별별로 투어 방문 횟수를 합산
            const genderTourVisits = d3.rollup(
                data,
                v => d3.sum(v, d => d['투어 방문 횟수']),
                d => d.성별
            );

            const genderDataArray = Array.from(genderTourVisits, ([key, value]) => ({ key, value }));
            setGenderData(genderDataArray);
        });
    }, []);

    useEffect(() => {
        if (genderData.length > 0) {
            // SVG 설정
            const svgWidth = 400;
            const svgHeight = 400;
            const radius = Math.min(svgWidth, svgHeight) / 2;

            const svg = d3.select(chartRef.current)
                .attr('width', svgWidth)
                .attr('height', svgHeight)
                .append('g')
                .attr('transform', `translate(${svgWidth / 2},${svgHeight / 2})`);

            // 색상 설정
            const colorScale = d3.scaleOrdinal()
                .domain(['남', '여'])
                .range(['#4A90E2', '#F78DA7']);

            // 파이 차트 설정
            const pie = d3.pie()
                .value(d => d.value)
                .sort(null);

            const arc = d3.arc()
                .outerRadius(radius - 10)
                .innerRadius(0);

            // 파이 데이터 생성
            const pieData = pie(genderData);

            // 파이 차트 그리기
            svg.selectAll('path')
                .data(pieData)
                .enter()
                .append('path')
                .attr('d', arc)
                .attr('fill', d => colorScale(d.data.key))
                .attr('stroke', '#fff')
                .style('stroke-width', '2px');

            // 퍼센트 표시
            svg.selectAll('text')
                .data(pieData)
                .enter()
                .append('text')
                .attr('transform', d => `translate(${arc.centroid(d)})`)
                .attr('text-anchor', 'middle')
                .attr('font-size', '14px')
                .attr('fill', '#fff')
                .text(d => `${Math.round((d.data.value / d3.sum(genderData, d => d.value)) * 100)}%`);
        }
    }, [genderData]);

    return (
        <div>
            <h3>성별에 따른 투어 방문 횟수</h3>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default GenderPieChart;
