import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const SatisfactionHeatmap = () => {
    const chartRef = useRef();
    const [matrixData, setMatrixData] = useState([]);
    const [allTours, setAllTours] = useState([]);
    const [allCategories, setAllCategories] = useState([]);

    useEffect(() => {
        // 1) merged_df.json 로드
        d3.json('/merged_df.json').then(rawData => {
            if (!rawData || !Array.isArray(rawData)) {
                console.error("merged_df.json is invalid or not array");
                return;
            }

            // 2) Step 1: '응답자' & '만족도(percent)' => meltdown
            let satisfactionData = [];

            rawData.forEach(row => {
                // "방문투어"가 없으면 스킵
                if (!row || !row["방문투어"]) return;

                const tourName = row["방문투어"];
                let responders = 0;
                if (row["응답자"] && typeof row["응답자"] === 'object') {
                    responders = row["응답자"]["응답자 수"] ?? 0;
                }

                const satisfactionObj = row["만족도(percent)"];

                // 만족도(percent) 필드가 객체인지 체크
                if (satisfactionObj && typeof satisfactionObj === 'object') {
                    // 각 key/value를 펼치기
                    Object.entries(satisfactionObj).forEach(([key, value]) => {
                        // 예: key = "Guide(Percent)", value = 0.98
                        // value가 숫자인지 확인 (아닐 시 parseFloat 시도)
                        let numericVal = parseFloat(value);
                        if (isNaN(numericVal)) numericVal = 0;

                        satisfactionData.push({
                            방문투어: tourName,
                            "응답자 수": responders,
                            카테고리: key,  // 예: "Guide(Percent)"
                            만족도: numericVal
                        });
                    });
                }
            });

            // 3) (방문투어, 카테고리) => 평균(만족도)
            const grouped = d3.rollups(
                satisfactionData,
                v => d3.mean(v, d => d.만족도),
                d => d.방문투어,
                d => d.카테고리
            );
            // grouped 예: [
            //   [ "강남", [ ["Guide(Percent)", 0.9], ["Overall Experience(Percent)", 0.8], ... ] ],
            //   [ "경복궁", [ ["Guide(Percent)", 0.95], ... ] ],
            // ]

            // 4) 전체 "방문투어"와 "카테고리" 목록 추출
            const tourSet = new Set();
            const catSet = new Set();
            grouped.forEach(([tour, arr]) => {
                tourSet.add(tour);
                arr.forEach(([cat]) => catSet.add(cat));
            });
            const allToursArr = Array.from(tourSet).sort();
            const allCatsArr = Array.from(catSet).sort();

            // 5) 히트맵용 2D 데이터 => [{ 투어:..., 카테고리:..., 만족도:...}, ...]
            let finalMatrix = [];
            grouped.forEach(([tour, arr]) => {
                // arr => [ ["Guide(Percent)", 0.9], ["Overall Experience(Percent)", 0.8], ... ]
                const catMap = new Map(arr);
                allCatsArr.forEach(cat => {
                    const val = catMap.get(cat) ?? 0;
                    finalMatrix.push({
                        투어: tour,
                        카테고리: cat,
                        만족도: val
                    });
                });
            });

            setAllTours(allToursArr);
            setAllCategories(allCatsArr);
            setMatrixData(finalMatrix);
        });
    }, []);

    useEffect(() => {
        if (!matrixData.length) return;

        // Dimensions
        const svgWidth = 900;
        const svgHeight = 600;
        const margin = { top: 50, right: 50, bottom: 100, left: 130 };

        // Remove old svg content
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X = 카테고리
        const xScale = d3.scaleBand()
            .domain(allCategories)
            .range([margin.left, svgWidth - margin.right])
            .padding(0.05);

        // Y = 방문투어
        const yScale = d3.scaleBand()
            .domain(allTours)
            .range([margin.top, svgHeight - margin.bottom])
            .padding(0.05);

        // 만족도 범위
        const maxVal = d3.max(matrixData, d => d.만족도) ?? 1;
        const minVal = d3.min(matrixData, d => d.만족도) ?? 0;

        // 색상 스케일 (cool)
        // 필요하다면 interpolateWarm, interpolateCoolwarm 등 다른 함수 사용
        const colorScale = d3.scaleSequential()
            .domain([minVal, maxVal])
            .interpolator(d3.interpolateCool);

        // X축
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform', 'rotate(-45)')
            .style('text-anchor', 'end')
            .style('font-size', '12px');

        // Y축
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .selectAll('text')
            .style('font-size', '12px');

        // 사각형(타일) 그리기
        svg.selectAll('rect.tile')
            .data(matrixData)
            .enter()
            .append('rect')
            .attr('class', 'tile')
            .attr('x', d => xScale(d.카테고리))
            .attr('y', d => yScale(d.투어))
            .attr('width', xScale.bandwidth())
            .attr('height', yScale.bandwidth())
            .attr('fill', d => colorScale(d.만족도));

        // 사각형 안에 값 표시
        svg.selectAll('text.value')
            .data(matrixData)
            .enter()
            .append('text')
            .attr('class', 'value')
            .attr('x', d => xScale(d.카테고리) + xScale.bandwidth() / 2)
            .attr('y', d => yScale(d.투어) + yScale.bandwidth() / 1.6)
            .attr('text-anchor', 'middle')
            .attr('font-size', '12px')
            .attr('fill', '#fff')
            .text(d => d.만족도.toFixed(2));

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text('방문 투어별 카테고리 만족도 (Percent) 히트맵');

        // X label
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', svgHeight - 10)
            .attr('text-anchor', 'middle')
            .style('font-size', '14px')
            .text('카테고리');

        // Y label
        svg.append('text')
            .attr('transform', 'rotate(-90)')
            .attr('x', -(svgHeight / 2))
            .attr('y', margin.left - 100)
            .attr('text-anchor', 'middle')
            .style('font-size', '14px')
            .text('방문 투어');

    }, [matrixData, allTours, allCategories]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default SatisfactionHeatmap;
