import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const colorScale = d3.scaleSequential()
    .domain([0, 4]) // 연령대가 몇 개인지에 따라 동적 처리 가능
    .interpolator(d3.interpolateViridis);
const TourVisitRevisitChart = () => {
    const chartRef = useRef();
    const [finalData, setFinalData] = useState([]);

    useEffect(() => {
        // JSON 형태의 tour_stats_df 느낌의 데이터 로딩
        // 예) [
        //   { 투어코스: "강남", "방문 횟수": 13, "재방문 횟수": 5, "재방문율": 38.46 },
        //   { 투어코스: "경복궁", "방문 횟수": 30, "재방문 횟수": 10, "재방문율": 33.3 },
        //   ...
        // ]
        d3.json('/tour_stats.json').then(data => {
            if (!data || !Array.isArray(data)) {
                console.error("tour_stats.json is invalid or not array");
                return;
            }
            // 혹시 숫자 변환 등 필요 시 여기서 처리
            data.forEach(d => {
                d["방문 횟수"] = +d["방문 횟수"];
                d["재방문율"] = +d["재방문율"];
            });
            // 정렬 (원하시면 안 해도 됨)
            data.sort((a,b) => b["방문 횟수"] - a["방문 횟수"]);
            setFinalData(data);
        });
    }, []);

    useEffect(() => {
        if (!finalData.length) return; // 데이터 준비 안 됐으면 중단

        // 차트 크기
        const svgWidth = 1000;
        const svgHeight = 500;
        const margin = { top: 50, right: 70, bottom: 100, left: 70 };

        // 기존 내용 제거
        d3.select(chartRef.current).selectAll('*').remove();

        // SVG 생성
        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X축: 투어코스
        const xDomain = finalData.map(d => d.투어코스);
        const xScale = d3.scaleBand()
            .domain(xDomain)
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        // 왼쪽 Y축: 방문 횟수 (bar)
        const maxVisits = d3.max(finalData, d => d["방문 횟수"]) || 1;
        const yScaleLeft = d3.scaleLinear()
            .domain([0, maxVisits])
            .nice()
            .range([svgHeight - margin.bottom, margin.top]);

        // 오른쪽 Y축: 재방문율 (0~100%)
        const maxRate = d3.max(finalData, d => d["재방문율"]) || 100;
        const yScaleRight = d3.scaleLinear()
            .domain([0, Math.max(100, maxRate)]) // 최대 100%보다 크면 확장
            .range([svgHeight - margin.bottom, margin.top]);

        // 막대 그래프 (왼쪽 Y축)
        svg.selectAll('.bar')
            .data(finalData)
            .enter()
            .append('rect')
            .attr('class','bar')
            .attr('x', d => xScale(d.투어코스))
            .attr('y', d => yScaleLeft(d["방문 횟수"]))
            .attr('width', xScale.bandwidth())
            .attr('height', d => (svgHeight - margin.bottom) - yScaleLeft(d["방문 횟수"]))
            .attr('fill', d3.interpolateViridis(0.6));
        // or palette for each bar if you want:
        // .attr('fill',(d,i)=> d3.interpolateViridis(i/finalData.length))

        // 라인 그래프 (오른쪽 Y축)
        // 1) Line generator
        const lineGen = d3.line()
            .x(d => xScale(d.투어코스) + xScale.bandwidth()/2) // 막대 중앙
            .y(d => yScaleRight(d["재방문율"]));

        // 2) Path
        svg.append('path')
            .datum(finalData)
            .attr('fill','none')
            .attr('stroke','red')
            .attr('stroke-width',2)
            .attr('d', lineGen);

        // 3) Circle markers
        svg.selectAll('.dot')
            .data(finalData)
            .enter()
            .append('circle')
            .attr('class','dot')
            .attr('cx', d => xScale(d.투어코스) + xScale.bandwidth()/2)
            .attr('cy', d => yScaleRight(d["재방문율"]))
            .attr('r', 4)
            .attr('fill','red');

        // X축
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform','rotate(-45)')
            .style('text-anchor','end')
            .style('font-size','12px');

        // 왼쪽 Y축 (방문 횟수)
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScaleLeft).ticks(6))
            .selectAll('text')
            .style('font-size','12px');

        // 오른쪽 Y축 (재방문율)
        svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right},0)`)
            .call(d3.axisRight(yScaleRight).ticks(5))
            .selectAll('text')
            .style('font-size','12px');

        // Title
        svg.append('text')
            .attr('x', svgWidth/2)
            .attr('y', margin.top / 2)
            .attr('text-anchor','middle')
            .style('font-size','16px')
            .text("투어 코스별 방문 횟수 및 재방문율");

        // X label
        svg.append('text')
            .attr('x', svgWidth/2)
            .attr('y', svgHeight - 10)
            .attr('text-anchor','middle')
            .style('font-size','14px')
            .text("투어코스");

        // 왼쪽 Y label
        svg.append('text')
            .attr('transform','rotate(-90)')
            .attr('x',- (svgHeight/2))
            .attr('y', margin.left - 50)
            .attr('text-anchor','middle')
            .style('font-size','14px')
            .text("방문 횟수");

        // 오른쪽 Y label
        svg.append('text')
            .attr('transform','rotate(-90)')
            .attr('x',- (svgHeight/2))
            .attr('y', svgWidth - margin.right + 45)
            .attr('text-anchor','middle')
            .style('font-size','14px')
            .text("재방문율 (%)");

    }, [finalData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourVisitRevisitChart;
