import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const NationalityBarChart = () => {
    const chartRef = useRef();
    const [data, setData] = useState([]);

    // JSON 파일 가져오기
    useEffect(() => {
        fetch('/nationality_tour_visits.json') // public 폴더 기준 경로
            .then(response => response.json())
            .then(data => {
                // visitCount를 숫자로 변환
                const formattedData = data.map(item => ({
                    country: item['국적'],
                    visitCount: Number(item['투어 방문 횟수']),
                }));
                setData(formattedData);
            });
    }, []);

    // D3 차트 생성
    useEffect(() => {
        if (data.length === 0) return;

        // 그래프 크기와 여백 설정
        const svg = d3.select(chartRef.current)
            .attr('width', 1400) // 그래프 너비 조정
            .attr('height', 600); // 그래프 높이 조정

        const margin = { top: 40, right: 40, bottom: 150, left: 80 };
        const width = +svg.attr('width') - margin.left - margin.right;
        const height = +svg.attr('height') - margin.top - margin.bottom;

        // 팔레트 색상 스케일 설정
        const colorScale = d3.scaleOrdinal(d3.schemeSet3);

        const xScale = d3
            .scaleBand()
            .domain(data.map(d => d.country))
            .range([0, width])
            .padding(0.1);

        const yScale = d3
            .scaleLinear()
            .domain([0, d3.max(data, d => d.visitCount)])
            .nice()
            .range([height, 0]);

        const g = svg
            .append('g')
            .attr('transform', `translate(${margin.left},${margin.top})`);

        // X축
        g.append('g')
            .attr('transform', `translate(0,${height})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform', 'rotate(-45)')
            .style('text-anchor', 'end')
            .style('font-size', '12px'); // X축 텍스트 크기 조정

        // Y축
        g.append('g').call(d3.axisLeft(yScale));

        // 막대 그래프
        g.selectAll('.bar')
            .data(data)
            .enter()
            .append('rect')
            .attr('class', 'bar')
            .attr('x', d => xScale(d.country))
            .attr('y', d => yScale(d.visitCount))
            .attr('width', xScale.bandwidth())
            .attr('height', d => height - yScale(d.visitCount))
            .attr('fill', d => colorScale(d.country)); // 국적별 색상 적용
    }, [data]);

    return <svg ref={chartRef}></svg>;
};

export default NationalityBarChart;
