import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

const TourBubbleChart = () => {
    const chartRef = useRef();
    const [data, setData] = useState([]);

    useEffect(() => {
        // 1) JSON 로딩
        // 예) [
        //   {
        //     "방문투어": "경복궁",
        //     "평균 투어객 정원": 8.7,
        //     "만족도 평균": 0.9,
        //     "응답자 수": 21.0
        //   },
        //   ...
        // ]
        d3.json('/merged_satisfaction_df2.json').then(raw => {
            if (!raw || !Array.isArray(raw)) {
                console.error("merged_satisfaction_df2.json is invalid or not array");
                return;
            }
            // 숫자 변환
            const cleaned = raw.map(d => ({
                ...d,
                "평균 투어객 정원": +d["평균 투어객 정원"],
                "만족도 평균": +d["만족도 평균"],
                "응답자 수": +d["응답자 수"]
            }));
            setData(cleaned);
        });
    }, []);

    useEffect(() => {
        if (!data.length) return;

        // 차트 크기 설정
        const svgWidth = 800;
        const svgHeight = 600;
        const margin = { top: 50, right: 50, bottom: 70, left: 70 };

        // 기존 내용 제거
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X, Y 도메인 설정
        const xMin = d3.min(data, d => d["평균 투어객 정원"]) ?? 0;
        const xMax = d3.max(data, d => d["평균 투어객 정원"]) ?? 10;
        const yMin = d3.min(data, d => d["만족도 평균"]) ?? 0;
        const yMax = d3.max(data, d => d["만족도 평균"]) ?? 1;

        // X 스케일: 평균 투어객 정원
        const xScale = d3.scaleLinear()
            .domain([0, xMax * 1.1]) // 조금 여유
            .range([margin.left, svgWidth - margin.right]);

        // Y 스케일: 만족도 평균
        const yScale = d3.scaleLinear()
            .domain([0, yMax * 1.1])
            .range([svgHeight - margin.bottom, margin.top]);

        // 버블 크기: 응답자 수
        const rMin = 5;
        const rMax = 25;
        const respondersMin = d3.min(data, d => d["응답자 수"]) ?? 1;
        const respondersMax = d3.max(data, d => d["응답자 수"]) ?? 1;

        const rScale = d3.scaleSqrt()
            .domain([respondersMin, respondersMax])
            .range([rMin, rMax]);

        // 컬러: 여기서는 임의로 interpolateBlues
        // 투어코스별로 다르게? or 동일? 아래는 한 가지 색상 팔레트 사용 예
        const colorScale = d3.scaleSequential()
            .domain([0, xMax]) // x값 기준으로 색상 달리
            .interpolator(d3.interpolateBlues);

        // 툴팁 div
        const tooltip = d3.select("body").append("div")
            .style("position","absolute")
            .style("padding","8px")
            .style("border","1px solid #ccc")
            .style("border-radius","4px")
            .style("background","#fff")
            .style("pointer-events","none")
            .style("opacity",0);

        // 버블(원)
        svg.selectAll("circle")
            .data(data)
            .enter()
            .append("circle")
            .attr("cx", d => xScale(d["평균 투어객 정원"]))
            .attr("cy", d => yScale(d["만족도 평균"]))
            .attr("r", d => rScale(d["응답자 수"]))
            .attr("fill", d => colorScale(d["평균 투어객 정원"]))
            .attr("opacity", 0.8)
            .on("mouseover", (event, d) => {
                tooltip.transition().duration(200).style("opacity",1);
                tooltip.html(`
          <strong>${d["방문투어"]}</strong><br/>
          평균 투어객 정원: ${d["평균 투어객 정원"]}<br/>
          만족도 평균: ${d["만족도 평균"]}<br/>
          응답자 수: ${d["응답자 수"]}<br/>
        `)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 30) + "px");
            })
            .on("mouseout", () => {
                tooltip.transition().duration(200).style("opacity",0);
            });

        // X축
        svg.append("g")
            .attr("transform", `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .style("font-size","12px");

        // Y축
        svg.append("g")
            .attr("transform", `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale))
            .style("font-size","12px");

        // X라벨
        svg.append("text")
            .attr("x", svgWidth/2)
            .attr("y", svgHeight - 25)
            .attr("text-anchor","middle")
            .style("font-size","14px")
            .text("평균 투어객 정원");

        // Y라벨
        svg.append("text")
            .attr("transform","rotate(-90)")
            .attr("x", -(svgHeight/2))
            .attr("y", margin.left - 50)
            .attr("text-anchor","middle")
            .style("font-size","14px")
            .text("만족도 평균");

        // 타이틀
        svg.append("text")
            .attr("x", svgWidth/2)
            .attr("y", margin.top/2)
            .attr("text-anchor","middle")
            .style("font-size","16px")
            .text("Bubble Chart: Tour Size vs. Satisfaction vs. Responders");

    }, [data]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default TourBubbleChart;
