import React, { useEffect } from 'react';
import * as d3 from "d3";
import * as topojson from "topojson-client";

const NationalityMapChart = () => {
    useEffect(() => {
        // 기존 SVG 제거 (중복 생성 방지)
        d3.select("#world-map").select("svg").remove();

        // SVG 설정
        const width = 960;
        const height = 500;

        const svg = d3.select("#world-map")
            .append("svg")
            .attr("width", width)
            .attr("height", height);

        // 바다 색상 적용 (SVG 배경 설정)
        svg.append("rect")
            .attr("width", "100%")
            .attr("height", "100%")
            .attr("fill", "#cce5ff"); // 연한 파랑색

        // 지리 투영 설정
        const projection = d3.geoMercator().scale(150).translate([width / 2, height / 1.5]);
        const path = d3.geoPath().projection(projection);

        // 색상 스케일 설정 (각 나라에 고유 색상 적용)
        const colorScale = d3.scaleOrdinal(d3.schemeSet3);

        // 지도 데이터 불러오기
        d3.json("https://cdn.jsdelivr.net/npm/world-atlas@2/countries-110m.json")
            .then(worldData => {
                const countries = topojson.feature(worldData, worldData.objects.countries).features;

                // 방문 횟수 데이터 불러오기
                d3.json(process.env.PUBLIC_URL + "/nationality_tour_visits_english.json").then(visitData => {
                    const visitCounts = {};
                    visitData.forEach(d => {
                        visitCounts[d.country] = d.visitCount;
                    });

                    // 세계지도 그리기
                    svg.selectAll("path")
                        .data(countries)
                        .enter()
                        .append("path")
                        .attr("d", path)
                        .attr("fill", (d, i) => colorScale(i)) // 나라마다 다른 색상 적용
                        .attr("stroke", "#fff")
                        .on("mouseover", (event, d) => {
                            const countryName = d.properties.name;
                            const visitCount = visitCounts[countryName] || 0;
                            d3.select("#tooltip")
                                .style("left", `${event.pageX + 10}px`)
                                .style("top", `${event.pageY + 10}px`)
                                .style("opacity", 1)
                                .html(`<strong>${countryName}</strong><br>Visits: ${visitCount}`);
                        })
                        .on("mouseout", () => {
                            d3.select("#tooltip").style("opacity", 0);
                        });
                });
            });
    }, []);

    return (
        <div>
            <div id="world-map"></div>
            <div id="tooltip" style={{
                position: "absolute",
                backgroundColor: "#fff",
                padding: "5px 10px",
                border: "1px solid #ccc",
                borderRadius: "5px",
                pointerEvents: "none",
                opacity: 0
            }}></div>
        </div>
    );
};




export default NationalityMapChart;
