import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';

// 색상 팔레트(viridis 등). 여기서는 d3.interpolateViridis 사용 예시
const colorScale = d3.scaleSequential()
    .domain([0, 4]) // 연령대가 몇 개인지에 따라 동적 처리 가능
    .interpolator(d3.interpolateViridis);

const AgeGroupPreferenceBar = () => {
    const chartRef = useRef();
    const [finalData, setFinalData] = useState([]);

    useEffect(() => {
        // JSON 로딩
        d3.json('/tour_gender_data.json').then(rawData => {
            if (!rawData || !Array.isArray(rawData)) {
                console.error('tour_gender_data.json is invalid or not an array');
                return;
            }

            // Step 0) "연령대"를 '나이' 필드에서 복사 (이미 "early 20s", "late 20s" 등이 있다 가정)

            // Step 1) melt: 투어_1 ~ 투어_4를 하나의 "투어코스" 컬럼으로 합침
            // 파이썬 melt와 유사한 로직:
            let melted = [];
            rawData.forEach(row => {
                const ageGroup = row.나이;          // 이미 'early 20s' 등
                const numVisits = row['투어 방문 횟수'];
                const nationality = row['국적'];
                const gender = row['성별'];

                // 투어_1 ~ 투어_4
                ['투어_1','투어_2','투어_3','투어_4'].forEach(tKey => {
                    const tourValue = row[tKey];
                    if (tourValue) {
                        // NaN/null 아닌 것만
                        melted.push({
                            연령대: ageGroup,
                            성별: gender,
                            국적: nationality,
                            투어방문횟수: numVisits,
                            투어코스: tourValue
                        });
                    }
                });
            });

            // Step 2) groupby(['연령대','투어코스']).size()
            // => (연령대, 투어코스) => 선호 횟수
            // D3에서 rollups 사용
            const grouped = d3.rollups(
                melted,
                v => v.length,
                d => d.연령대,
                d => d.투어코스
            );
            // grouped 예: [ ["early 20s", [ ["경복궁", 10], ["낙산", 5], ... ]], ["late 20s", ...], ... ]

            // 최종 배열: [{ 연령대, 투어코스, 선호횟수 }, ...]
            let finalArr = [];
            grouped.forEach(([ageGroup, arr]) => {
                arr.forEach(([tourName, count]) => {
                    finalArr.push({
                        연령대: ageGroup,
                        투어코스: tourName,
                        선호횟수: count
                    });
                });
            });

            // 정렬: by [연령대, 선호횟수 desc]
            // (문자열 정렬 주의. 여기서는 단순 알파벳 정렬 가정)
            finalArr.sort((a,b) => {
                if (a.연령대 < b.연령대) return -1;
                if (a.연령대 > b.연령대) return 1;
                // same age => desc by count
                return b.선호횟수 - a.선호횟수;
            });

            setFinalData(finalArr);
        });
    }, []);

    useEffect(() => {
        if (!finalData.length) return;

        // 차트 크기
        const svgWidth = 1000;
        const svgHeight = 500;
        const margin = { top: 50, right: 150, bottom: 100, left: 70 };

        // 기존 svg 삭제
        d3.select(chartRef.current).selectAll('*').remove();

        const svg = d3.select(chartRef.current)
            .attr('width', svgWidth)
            .attr('height', svgHeight);

        // X축: 투어코스
        const xDomain = [...new Set(finalData.map(d => d.투어코스))];
        const xScale = d3.scaleBand()
            .domain(xDomain)
            .range([margin.left, svgWidth - margin.right])
            .padding(0.2);

        // hue: 연령대 => grouped bar
        // 여기서는 seaborn.barplot(..., hue="연령대")에 상응
        // D3는 Grouped Bar로도, Stacked Bar로도 가능
        // *파이썬은 단일 barplot + hue => 자동 그룹화*
        // 여기서는 "그룹 바"로 직접 구현

        // 1) We can do a single bar with color=ageGroup, but that lumps them all
        // 2) We can do x0=투어코스, x1=연령대 => grouped bar
        //    => But in Python's barplot, it merges them by hue, so let's do that

        // Step: we need [투어코스, {age1: count, age2: count,...}]
        const ageSet = new Set(finalData.map(d => d.연령대));
        const allAges = Array.from(ageSet);

        // pivot -> [ { 투어코스, age1:?, age2:?, ...}, ... ]
        let pivotData = [];
        xDomain.forEach(tour => {
            let row = { 투어코스: tour };
            allAges.forEach(a => row[a] = 0);
            pivotData.push(row);
        });
        // fill
        finalData.forEach(d => {
            let row = pivotData.find(r => r.투어코스 === d.투어코스);
            if (row) {
                row[d.연령대] = d.선호횟수;
            }
        });

        // x0=투어코스, x1=연령대
        const x1 = d3.scaleBand()
            .domain(allAges)
            .range([0, xScale.bandwidth()])
            .padding(0.05);

        const maxCount = d3.max(pivotData, row => {
            return d3.max(allAges, age => row[age]);
        });
        const yScale = d3.scaleLinear()
            .domain([0, maxCount])
            .nice()
            .range([svgHeight - margin.bottom, margin.top]);

        // color: viridis-like
        const color = d3.scaleOrdinal()
            .domain(allAges)
            .range(d3.quantize(d3.interpolateViridis, allAges.length));

        // group for each tour
        const gTour = svg.selectAll('.gTour')
            .data(pivotData)
            .enter()
            .append('g')
            .attr('class', 'gTour')
            .attr('transform', d => `translate(${xScale(d.투어코스)},0)`);

        // draw bars
        gTour.selectAll('rect')
            .data(d => {
                // d => { 투어코스:..., "early 20s":?, "late 20s":?, ...}
                return allAges.map(age => ({ age, value: d[age], tour: d.투어코스 }));
            })
            .enter()
            .append('rect')
            .attr('x', d => x1(d.age))
            .attr('y', d => yScale(d.value))
            .attr('width', x1.bandwidth())
            .attr('height', d => (svgHeight - margin.bottom) - yScale(d.value))
            .attr('fill', d => color(d.age));

        // X axis
        svg.append('g')
            .attr('transform', `translate(0,${svgHeight - margin.bottom})`)
            .call(d3.axisBottom(xScale))
            .selectAll('text')
            .attr('transform', 'rotate(-90)')
            .attr('dx', '-0.6em')
            .attr('dy', '-0.1em')
            .style('text-anchor', 'end')
            .style('font-size', '10px');

        // Y axis
        svg.append('g')
            .attr('transform', `translate(${margin.left},0)`)
            .call(d3.axisLeft(yScale).ticks(6));

        // Title
        svg.append('text')
            .attr('x', svgWidth / 2)
            .attr('y', margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '16px')
            .text('투어코스별 연령대 선호 횟수');

        // Legend
        const legend = svg.append('g')
            .attr('transform', `translate(${svgWidth - margin.right + 20}, ${margin.top})`);

        allAges.forEach((age, i) => {
            const legendRow = legend.append('g')
                .attr('transform', `translate(0, ${i * 20})`);

            legendRow.append('rect')
                .attr('width', 15)
                .attr('height', 15)
                .attr('fill', color(age));

            legendRow.append('text')
                .attr('x', 20)
                .attr('y', 12)
                .style('font-size', '12px')
                .text(age);
        });

    }, [finalData]);

    return (
        <div>
            <svg ref={chartRef}></svg>
        </div>
    );
};

export default AgeGroupPreferenceBar;
